<!--  -->
<template>
<div class='updated-photo'>
    <!--  -->
    <!--  -->
    <img :src="imgPath" ref="imgRef"/>
    <div class="footer">
        <div @click="onCancel"></div>
        <div @click="onConfirm"></div>
    </div>
</div>
</template>

<script>
// jsjsjson
// import  from '';
import 'cropperjs/dist/cropper.css'
import Cropper from 'cropperjs'
import { updateUserAvatar } from '@/api/user.js'
let cropper
export default {
// import
  name: 'UpdatePhoto',
  props: { imgPath: [Object, String] },
  components: {},
  data  () {
    // 
    return {

    }
  },
  //  data
  computed: {},
  // data
  watch: {},
  // 
  methods: {
    onCancel () {
      this.$emit('close') // 
    },
    onConfirm () {
      // 
      cropper.getCroppedCanvas().toBlob(async (blob) => {
        console.log(blob)
        // 
        const formData = new FormData()
        formData.append('photo', blob)
        // 
        const res = await updateUserAvatar(formData)
        // 
        this.$toast.success('')
        // 
        this.$emit('input', res.data.data.photo)
        // 
        this.$emit('close')
      })
    }
  },
  //  - this
  created  () {

  },
  //  - DOM
  mounted  () {
    const image = this.$refs.imgRef
    cropper = new Cropper(image, {
      viewMode: 1, // 
      dragMode: 'move', // 
      aspectRatio: 1, // 
      autoCropArea: 1, // 
      cropBoxMovable: false, // 
      cropBoxResizable: false, // 
      background: false // 
    })
    console.log(cropper)
  },
  beforeCreate  () {}, //  - 
  beforeMount  () {}, //  - 
  beforeUpdate  () {}, //  - 
  updated  () {}, //  - 
  beforeDestroy  () {}, //  - 
  destroyed  () {}, //  - 
  activated  () {} // keep-alive
}
</script>
<style lang='less' scoped>
// @import url(); css
.updated-photo{
    background-color: black;
    height:100%;
}
.footer {
      position: fixed;
    bottom: 0;
    left: 0;
    width: 100%;
    display: flex;
    justify-content: space-between;
    font-size: 36px;
    padding: 20px;
    box-sizing: border-box;
    color: #fff;
}
</style>
