<!--  -->
<template>
  <div class="">
    <van-picker
      title=""
      show-toolbar
      :columns="columns"
      @confirm="onConfirm"
      @cancel="onCancel"
      @change="onChange"
      :default-index="value"
    ></van-picker>
    <!--  -->
  </div>
</template>

<script>
// jsjsjson
// import  from '';

import { updateUserProfile } from '@/api/user.js'

export default {
// import
  name: 'UpdateGender',
  props: { value: Number }, // 
  components: {},
  data  () {
    // 
    return {
      columns: ['', ''],
      currentValue: this.value // 
    }
  },
  //  data
  computed: {},
  // data
  watch: {},
  // 
  methods: {
    async onConfirm (value, index) {
      // 
    // 
      await updateUserProfile({ gender: this.currentValue })
      // 
      this.$toast.success('')
      // 
      this.$emit('input', this.currentValue)
      // 
      this.$emit('close')
    },
    onChange  (picker, value, index) {
      this.currentValue = index // 
    },
    onCancel () {
      this.$emit('close') // 
    }
  },
  //  - this
  created  () {

  },
  //  - DOM
  mounted  () {

  },
  beforeCreate  () {}, //  - 
  beforeMount  () {}, //  - 
  beforeUpdate  () {}, //  - 
  updated  () {}, //  - 
  beforeDestroy  () {}, //  - 
  destroyed  () {}, //  - 
  activated  () {} // keep-alive
}
</script>
<style lang='less' scoped>
