<!--  -->
<template>
  <div class="">
    <!--  -->
    <van-datetime-picker
      v-model="currentDate"
      type="date"
      title=""
      :min-date="minDate"
      :max-date="maxDate"
      @cancel="onCancel"
      @confirm="onConfirm"
    ></van-datetime-picker>
  </div>
</template>

<script>
// jsjsjson
// import  from '';
import dayjs from 'dayjs'
import { updateUserProfile } from '@/api/user.js'
export default {
// import
  name: 'UpdateBirthday',
  props: {},
  components: {},
  data  () {
    // 
    return {
      minDate: new Date(1900, 0, 1),
      maxDate: new Date(),
      currentDate: new Date(this.value)
    }
  },
  //  data
  computed: {},
  // data
  watch: {},
  // 
  methods: {
    // 
    onCancel () {
      this.$emit('close')
    },
    async onConfirm () {
      const birthday = dayjs(this.currentDate).format('YYYY-MM-DD')
      console.log('birthday', birthday)
      // 
      await updateUserProfile({ birthday })
      // 
      this.$toast.success('')
      // 
      this.$emit('input', birthday)
      // 
      this.$emit('close')
    }
  },
  //  - this
  created  () {

  },
  //  - DOM
  mounted  () {

  },
  beforeCreate  () {}, //  - 
  beforeMount  () {}, //  - 
  beforeUpdate  () {}, //  - 
  updated  () {}, //  - 
  beforeDestroy  () {}, //  - 
  destroyed  () {}, //  - 
  activated  () {} // keep-alive
}
</script>
