<!--  -->
<template>
  <div class="">
    <van-nav-bar
      title=""
      left-text=""
      right-text=""
      @click-left="onCancel"
      @click-right="onConfirm"
    />
    <div class="box">
      <van-field
        v-model="message"
        rows="3"
        maxlength="9"
        show-word-limit
        autosize
        type="textarea"
        placeholder=""
      />
    </div>
  </div>
</template>

<script>
import { updateUserProfile } from '@/api/user'
// jsjsjson
// import  from '';

export default {
// import
  name: 'UpdateName',
  props: { value: String },
  components: {},
  data  () {
    // 
    return {
      message: this.value
    }
  },
  //  data
  computed: {},
  // data
  watch: {},
  // 
  methods: {
    onCancel () {
      // 1 - 
      // this.message=this.value
      this.$emit('close')
    },
    async onConfirm () {
      // debugger
      // debugger
      // 
      if (this.message.length === 0) return
      // 
      await updateUserProfile({ name: this.message })
      // 
      this.$toast.success('')
      // 
      this.$emit('input', this.message)
      // 
      this.$emit('close')
    }
  },
  //  - this
  created  () {

  },
  //  - DOM
  mounted  () {

  },
  beforeCreate  () {}, //  - 
  beforeMount  () {}, //  - 
  beforeUpdate  () {}, //  - 
  updated  () {}, //  - 
  beforeDestroy  () {}, //  - 
  destroyed  () {}, //  - 
  activated  () {} // keep-alive
}
</script>
<style lang='less' scoped>
// @import url(); css
.box {
  padding: 20px;
}
</style>
